}
:root {
    --input-inverted-clear-icon: url('https://alfabank.st/icons/glyph_cross-circle_m.svg');
    --input-inverted-color: var(--color-light-text-primary-inverted);
    --input-inverted-placeholder-color: var(--color-light-text-secondary-inverted-transparent);
    --input-inverted-focus-placeholder-color: var(--color-light-text-tertiary-inverted);
    --input-inverted-with-label-placeholder-color: var(--color-light-text-tertiary-inverted);
    --input-inverted-error-icon: url('https://alfabank.st/icons/glyph_alert-circle_m_negative.svg');
    --input-inverted-success-icon: url('https://alfabank.st/icons/icon_ok_s_color.svg');
    --input-inverted-caret-color: var(--input-inverted-color);

    /* disabled */
    --input-inverted-disabled-color: var(--color-light-text-secondary-inverted-transparent);

    /* read-only */
    --input-inverted-read-only-color: var(--color-light-text-secondary-inverted-transparent);

    /* error */
    --input-inverted-error-color: var(--color-light-text-primary-inverted);
}
.input__input_10ckc {
    color: var(--input-inverted-color);
    caret-color: var(--input-inverted-caret-color);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.input__input_10ckc:-webkit-autofill {
    -webkit-text-fill-color: var(--input-inverted-color);
}
.input__input_10ckc::placeholder {
    color: var(--input-inverted-placeholder-color);
}
.input__hasLabel_10ckc::placeholder {
    color: var(--input-inverted-with-label-placeholder-color);
}
.input__clearIcon_10ckc {
    background-image: var(--input-inverted-clear-icon);
}
.input__errorIcon_10ckc {
    background-image: var(--input-inverted-error-icon);
}
.input__successIcon_10ckc {
    background-image: var(--input-inverted-success-icon);
}
/* READ-ONLY STATE */
.input__input_10ckc:read-only {
    color: var(--input-inverted-read-only-color)
}
.input__input_10ckc:read-only:focus::placeholder {
        color: var(--input-inverted-placeholder-color);
    }
/* DISABLED STATE */
.input__input_10ckc:disabled {
    color: var(--input-inverted-disabled-color);
    -webkit-text-fill-color: var(--input-inverted-disabled-color);
}
/* FOCUS STATE */
.input__input_10ckc:focus::placeholder {
    color: var(--input-inverted-focus-placeholder-color);
}
/* ERROR STATE */
.input__error_10ckc {
    color: var(--input-inverted-error-color);
    caret-color: var(--input-inverted-error-color);
}
